body {
    background-color: #eee ;
    font-family: "Segoe Ui Light" ;
}


#main h1 {
    color: #444;
    text-align: center;
}

#main {
    padding: 0px;
    box-sizing: border-box;
    width: 60%;
    height: 100%;
}

.books-contanier {
    height: 400px;
    overflow: auto;
}

#book-list {
    padding: 0px;
}

#book-list li {
    display: inline-block;
    margin: 12px;
    padding: 10px;
    border-radius: 4px;
    border: #880e4f 1px solid;
    box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    color: #880e4f;
}

form {
    background: #fff;
    padding: 20px;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 400px;
}

form .field {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

form label {
    text-align: right;
    padding: 6px;
}

form select , form input {
    margin: 4px;
    padding: 6px;
    box-sizing: border-box;
}

form button {
    color: #fff;
    font-size: 2em;
    background: #ad1457 ;
    border: 0;
    padding: 0px 10px;
    border-radius: 50%;
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    left: 10px;
}


#book-details {
    position: fixed;
    top: 0;
    right: 0;
    width: 40%;
    height: 100%;
    background: #ad1457 ;
    padding: 30px;
    overflow: auto;
    box-shadow: -2px -3px 5px rgba(0, 0, 0, 0.3) ;
    box-sizing: border-box;
    color: #fff;
}


.fz-3 {
    font-size: 1.6rem;
}